body {
  margin: 0;
  font-family: "coder", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


body::-webkit-scrollbar {
  display: none;
}

body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

html {
  background-color: var(--primary);
}


/* Add custom fonts */

@font-face {
  font-family: "coder";
  src: local("coder"), url(font.woff) format("woff");
}

@font-face {
  font-family: 'PiecesOfEight';
  src: local('PiecesOfEight'), url("../fonts/Pieces_of_Eight.ttf") format('truetype');
}

@font-face {
  font-family: 'PopinsLight';
  src: local('PopinsLight'), url("../fonts/Poppins-Light.ttf") format('truetype');
}

@font-face {
  font-family: 'PopinsBold';
  src: local('PopinsBold'), url("../fonts/Poppins-Bold.ttf") format('truetype');
}

#content {
  background: rgba( 142, 91, 91, 0.35 );
  box-shadow: 0 8px 32px 0 rgba(52, 62, 169, 0.37);
  backdrop-filter: blur( 6.5px );
  -webkit-backdrop-filter: blur( 6.5px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
}



